body{
    background-color: #e5e5e5;
}

.main_container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 2rem;
}
.profileImage{
    width: 50%;
    height: auto;
    border-radius: 49%;
}
.homepage_content1{
    text-align: center;
    margin: 4px;
    font-family: system-ui;
    margin-top: 30px;
    padding: 4px;
    font-size: 1.5rem;
}
