.homepage_btn_div{
    display: flex;
}
.homepage_content2{
    text-align: center;
    font-family: system-ui;
    padding-left: 25px;
    padding-right: 25px;
}

.homepage_btn1, .homepage_btn2{
    margin: 1rem;
    width: 7.4rem;
    height: 2.3rem;
    border: 2px solid gray;
    background-color: #ffc107;
    font-weight: bold;
    outline: none;
    border-radius: 17px;
    font-size: 0.85rem;
}
.homepage_btn1:hover, .homepage_btn2:hover{
    color: black;
    background-color: rgb(238, 224, 34);
    
}

.homepage_btn2 a, .homepage_btn1 a{
    text-decoration: none;
    color: #58453f;
}

.txt-rotate{
    color: #ff5722;
}