.block{
    /* margin-left: 74px; */
    background-color: #d57777;
    border: 2px solid;
    border-radius: 20px;
    display: flex;
    width: 65%;
    height: auto;
    margin: 1rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: 0px 3px 6px 4px grey;
}
.block:hover{
    box-shadow: 1px 4px 6px 5px grey;

}
.block_img{
    filter: grayscale(58%);
    border-radius: 18px;
    width: 100%;
    /* height: 167px; */
    border: 2px solid gray;
}
.block_img:hover{
    filter: grayscale(0%);
}

.details{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.block_details{
    margin: 2px;
    font-family: sans-serif;
    border-bottom: 1px solid;
    padding: 2px;
}
.block_quote{
    margin: 2px;
    font-family: sans-serif;
}
#block_link{
    border-top: 2px solid black; 
    border-bottom-right-radius: 18px;
    border-bottom-left-radius: 18px;
    width: 100%;
    /* background: transparent; */
    color: rgb(61, 61, 231);
    font-family: cursive;
    border: none;
    outline: none;
    cursor: pointer;
}
